<template>
  <el-card class="box-card">
    <div class="title">
      {{title}}
      <el-button
        style="float: right; position: relative; top:0px;"
        type="danger"
        icon="el-icon-back"
        circle
        plain
        @click="out"
      ></el-button>
    </div>
    <br />
    <div>
      <div>
        <div class="left"></div>
        <el-input placeholder="" v-model="blogtitle" clearable style="width: 1140px;"></el-input>
      </div>
      <br />
      <div class="left"></div>
      <div style="text-align: left;">
        <el-select v-model="sort" filterable allow-create placeholder="">
          <el-option
            v-for="(item,index) in options"
            :key="index"
            :label="item.category"
            :value="item.category"
          ></el-option>
          <!---->
        </el-select>
        <el-button
          type="primary"
          plain
          icon="el-icon-circle-plus-outline"
          style="position: relative; left: 20px;"
          @click="Visible = true"
        ></el-button>
        <el-button
          type="danger"
          plain
          icon="el-icon-delete"
          style="position: relative; left: 25px;"
          @click="delsort"
        ></el-button>
      </div>
      <br />
      <div class="left"></div>
      <br />
      <div>
        <quill-editor v-model="editorHtml" ref="myQuillEditor" :options="editorOption"></quill-editor>
        <el-upload
          class="avatar-uploader"
          action="#"
          :http-request="Uploading"
          :show-file-list="false"
          :before-upload="beforeAvatarUpload"
          style="display: none"
        >
          <i class="el-icon-plus avatar-uploader-icon" id="imgInput"></i>
        </el-upload>
      </div>
    </div>
    <br />
    <br />
    <br />
    <div>
      <el-button type="primary" icon="el-icon-s-promotion" @click="addboke" v-show="btn"></el-button>
      <el-button type="primary" icon="el-icon-edit" @click="changeboke" v-show="!btn"></el-button>
    </div>

    <el-dialog title="" :visible.sync="Visible" width="30%" :show-close="false">
      <el-form label-width="100px" class="demo-ruleForm">
        <el-form-item label="">
          <el-input
            type="text"
            v-model="sortinput"
            style="width: 300px;"
            clearable
            @keydown.enter.native="addsort"
          ></el-input>
        </el-form-item>
        <el-button type="primary" @click="addsort" style="width: 400px;" plain size="small"></el-button>
        <br />
        <br />
        <el-button @click="Visible = false" style="width: 400px;" plain size="small"></el-button>
      </el-form>
    </el-dialog>
  </el-card>
</template>

<script>
import { quillEditor } from "vue-quill-editor";
import { quillRedefine } from "vue-quill-editor-upload";

export default {
  components: { quillEditor },
  data() {
    return {
      sortinput: "",
      Visible: false, //
      editorHtml: "", //
      editorOption: {
        placeholder: ""
      }, //
      title: "",
      btn: true, //
      blogtitle: "",
      sort: "", //
      options: [] //
    };
  },
  mounted() {
    var title = this.$route.params.title;
    if (title != undefined) {
      this.change();
    }
    this.loadingcategory();
    this.$refs.myQuillEditor.quill
      .getModule("toolbar")
      .addHandler("image", this.imgHandler);
  },
  methods: {
    beforeAvatarUpload(file) {  //
      const isJPG = ["image/jpeg", "image/png", "image/jpg"].includes(
        file.type
      );
      const isLt2M = file.size / 1024 / 1024 < 1;

      if (!isJPG) {
        this.$message.error("!");
      }
      if (!isLt2M) {
        this.$message.error(" 1MB!");
      }
      return isJPG && isLt2M;
    },
    imgHandler(state) {  //
      this.addRange = this.$refs.myQuillEditor.quill.getSelection();
      if (state) {
        let fileInput = document.getElementById("imgInput");
        fileInput.click(); // 
      }
    },
    Uploading(data) {  // 
      // console.log(data);
      var that = this;
      const name = window.sessionStorage.getItem("name");
      this.getBase64(data.file).then(resBase64 => {
        // this.fileBase64 = resBase64.split(",")[1]; //base64
        // console.log(resBase64);
        this.$axios
          .post("./addBlogImgPost", {
            base64Data: resBase64
          })
          .then(function(res) {
            let value = res.data.src;
            // console.log(value);
            let quill = that.$refs.myQuillEditor.quill;
            let length = quill.getSelection().index;
            //  response.data.url
            quill.insertEmbed(length, "image", value);
            // 
            quill.setSelection(length + 1);
          });
      });
    },
    getBase64(file) {  // base64
      return new Promise((resolve, reject) => {
        let reader = new FileReader();
        let fileResult = "";
        reader.readAsDataURL(file); //
        reader.onload = function() {
          fileResult = reader.result;
        }; //
        reader.onerror = function(error) {
          reject(error);
        }; // resolve 
        reader.onloadend = function() {
          // console.log(fileResult);
          resolve(fileResult);
        };
      });
    },
    change() {  //
      this.btn = false;
      var title = this.$route.params.title;
      var body = this.$route.params.body;
      var category = this.$route.params.category;
      // console.log(title,body);
      this.title = "";
      this.blogtitle = title;
      this.editorHtml = body;
      this.sort = category;
      window.sessionStorage.setItem("oldtitle", title);
    },
    changeboke() {  //
      const user = window.sessionStorage.getItem("user");
      const pass = window.sessionStorage.getItem("pass");
      const oldtitle = window.sessionStorage.getItem("oldtitle");
      // console.log(oldtitle)
      var that = this;
      this.$axios
        .get("./ChangeBlog", {
          params: {
            blogtitle: that.blogtitle,
            blogbody: that.editorHtml,
            username: user,
            password: pass,
            oldtitle: oldtitle,
            category: that.sort
          }
        })
        .then(function(res) {
          if (res.data == "yes") {
            that.$message({
              message: "",
              type: "success"
            });
            that.$router.push("/myboke");
          } else {
            that.$message.error("");
          }
        });
    },
    loadingcategory() {  //
      const user = window.sessionStorage.getItem("user");
      const pass = window.sessionStorage.getItem("pass");
      var that = this;
      this.$axios
        .get("./searchAllCategory", {
          params: {
            username: user,
            password: pass
          }
        })
        .then(function(res) {
          // console.log("res",res.data);
          that.options = res.data;
        });
    },
    addsort() {  //
      if (this.sortinput != "") {
        const user = window.sessionStorage.getItem("user");
        const pass = window.sessionStorage.getItem("pass");
        var that = this;
        this.$axios
          .get("./addCategory", {
            params: {
              username: user,
              password: pass,
              category: that.sortinput
            }
          })
          .then(function(res) {
            that.$message({
              message: "",
              type: "success"
            });
            that.Visible = false;
            that.loadingcategory();
            that.sortinput = "";
          });
      } else {
        this.$message.error("");
      }
    },
    delsort() {  //
      if (this.sort != "") {
        this.$confirm(", ?", "", {
          confirmButtonText: "",
          cancelButtonText: "",
          type: "warning"
        })
          .then(() => {
            const user = window.sessionStorage.getItem("user");
            const pass = window.sessionStorage.getItem("pass");
            var that = this;
            this.$axios
              .get("./deleteCategory", {
                params: {
                  category: that.sort,
                  username: user,
                  password: pass
                }
              })
              .then(function(res) {
                // console.log(res)
                that.$message({
                  message: "",
                  type: "success"
                });
                that.sort = "";
                that.loadingcategory();
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: ""
            });
          });
      } else {
        this.$message.error("");
      }
    },
    addboke() {  //
      if (this.blogtitle == "" || this.editorHtml == "" || this.sort == "") {
        this.$message({
          center: true,
          showClose: true,
          message: "",
          type: "warning"
        });
      } else {
        const name = window.sessionStorage.getItem("name");
        const user = window.sessionStorage.getItem("user");
        const pass = window.sessionStorage.getItem("pass");
        var that = this;
        // console.log(that.sort)
        this.$axios
          .get("./AddBlog", {
            params: {
              identity1: name,
              blogbody: that.editorHtml,
              blogtitle: that.blogtitle,
              username: user,
              password: pass,
              category: that.sort //
            }
          })
          .then(function(res) {
            // console.log(res);
            if (res.data == "yes") {
              that.$message({
                message: "",
                type: "success"
              });
              that.$router.push("/myboke");
            } else {
              that.$message.error("");
            }
          });
      }
    },
    out() {
      //
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.$router.go(-1);
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>

<style scoped>
.title {
  font-size: 25px;
  color: #000;
}
.left {
  font-weight: bold;
  font-size: 15px;
  float: left;
}

.quill-editor {
  height: 300px;
}
</style>