<template>
  <div>
    <el-card>
      <div style="float:left;">
        <div style="display: inline-block;">
          <el-avatar shape="square" fit="cover" :src="imgurl"></el-avatar>
        </div>
        <div class="title">
          <div style="font-weight: bold; font-size: 20px;">{{usermessg.identity1}}</div>
          <br />
          <br />
          <div style="color:#909399;">{{usermessg.time1}}</div>
        </div>
        <ul>
          <li>
            <p style="color:#909399;"></p>
            <p style="color:#606266;">{{usermessg.sex}}</p>
          </li>
          <li>
            <p style="color:#909399;"></p>
            <p style="color:#606266;">{{blognum}}</p>
          </li>
          <li>
            <p style="color:#909399;"></p>
            <p style="color:#606266;">{{bloghot}}</p>
          </li>
          <li>
            <p style="color:#909399;"></p>
            <p style="color:#606266;">{{commentnum}}</p>
          </li>
          <li>
            <p style="color:#909399;"></p>
            <p style="color:#606266;">{{concern}}</p>
          </li>
          <li>
            <p style="color:#909399;"></p>
            <p style="color:#606266;">{{fans}}</p>
          </li>
        </ul>
        <div class="concernbtn" v-show="concernbtnshow">
           <el-button type="primary" round @click="concerta"></el-button>
        </div>
        <div class="concernbtn" v-show="!concernbtnshow">
           <el-button type="info" round @click="concerta"></el-button>
        </div>
      </div>
    </el-card>

    <div style="width: 800px; display: inline-block; float: left;">
      <el-card>
        <el-table :data="tableData" style="width: 100%;">
          <el-table-column label="" type="index"></el-table-column>
          <el-table-column label="" prop="blogtitle" width="200px"></el-table-column>
          <el-table-column label="" prop="blogbody" width="160px"></el-table-column>
          <el-table-column label="" prop="popularity1" width="80px"></el-table-column>
          <el-table-column label="" prop="time1" width="180px"></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-view"
                @click="handleView(scope.$index, scope.row)"
                circle
              ></el-button>
            </template>
          </el-table-column>
        </el-table><br>
        <el-pagination
          background
          @current-change="bokechange"
          layout="prev, pager, next,total"
          :page-size="size"
          :total="total"
        ></el-pagination>
        <!---->
      </el-card>
    </div>

    <div style="width: 460px; display: inline-block; float: right;">
      <el-card>
        <div style="text-align: left; color:#909399;">
          
          <el-card class="box-card">
            <div>{{usermessg.address}}</div>
          </el-card>
        </div>
        <br />
        <div style="text-align: left; color:#909399;">
          
          <el-card class="box-card">
            <div>{{usermessg.personShow}}</div>
          </el-card>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      size: 10, //5
      total: 0, //
      tableData: [], //
      imgurl: "", //
      blognum: "", //
      bloghot: "", //
      usermessg: {}, //TA
      identity1: "", //TA
      commentnum: "", //
      fans:"",  //
      bokename:'',    
      concern:"", //
      concernbtnshow:true,  //
    };
  },
  mounted() {
    this.loding();
    this.bokechange();
    this.searchIfFollow();
  },
  methods: {
    loding() {  //
      var name = this.$route.params.name;
      this.bokename = name;
      if (name != undefined) {
        window.sessionStorage.setItem("othername", name);
      }
      const othername = window.sessionStorage.getItem("othername");
      this.identity1 = othername;
      const user = window.sessionStorage.getItem("user");
      const pass = window.sessionStorage.getItem("pass");
      var that = this;
      this.$axios
        .get("./otherUserData", {
          params: {
            username: user,
            password: pass,
            identity1: othername
          }
        })
        .then(function(res) {
          // console.log(res.data);
          if (res.data.user.address == "" || res.data.user.personShow == "") {
            res.data.user.address = "";
            res.data.user.personShow = "";
          }
          that.usermessg = res.data.user;
          that.bloghot = res.data.popularity1;
          that.blognum = res.data.number;
          that.commentnum = res.data.commentCount[0].commentCount;
        });
      this.$axios
        .get("./searchImg?identity1=" + othername)
        .then(function(res) {
          //   console.log(res.data);
          that.imgurl =  './' + res.data;
        });
      this.$axios.get("./searchCount",{
        params:{
          username: user,
          password: pass,
          identity1: othername
        }
      }).then(function(res){
        // console.log(res.data);
        that.fans = res.data.fansCount[0].fansCount;
        that.concern = res.data.followCount[0].followCount;
      })
    },
    bokechange(page = 1) {  //
      const user = window.sessionStorage.getItem("user");
      const pass = window.sessionStorage.getItem("pass");
      var that = this;
      this.$axios
        .get("./searchOtherUserBlog", {
          params: {
            username: user,
            password: pass,
            identity1: that.identity1,
            page: page
          }
        })
        .then(function(res) {
          //   console.log(res.data);
          that.total = res.data.count[0].count;
          for (var i = 0; i < res.data.AllBlogs.length; i++) {
            res.data.AllBlogs[i].blogbody = res.data.AllBlogs[
              i
            ].blogbody.replace(/[\x00-\xff]/gi, ""); //
            res.data.AllBlogs[i].blogbody = res.data.AllBlogs[
              i
            ].blogbody.substring(0, 25); //100
            if (res.data.AllBlogs[i].blogbody == "") {
              res.data.AllBlogs[i].blogbody = "";
            }
          }
          that.tableData = res.data.AllBlogs;
          window.scrollTo(0,0);
        });
    },
    handleView(index, row) {  //
      // console.log(index,row)
      var title = row.blogtitle;
      this.$router.push({
        name: "viewboke", //
        params: {
          title: title,
          bokename:this.bokename,
        }
      });
    },
    concerta(){     //TA
      // this.concernbtnshow = !this.concernbtnshow;
      const name = window.sessionStorage.getItem("name");
      const user = window.sessionStorage.getItem("user");
      const pass = window.sessionStorage.getItem("pass");
      var that = this;
      if(this.concernbtnshow == false){
        this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.$axios.get("./addFollow",{
            params:{
              identity1:that.identity1,
              identity2:name,
              username: user,
              password: pass,
            }
          }).then(function(res){
            // console.log(res.data)
            if(res.data == "yes"){
              that.$message({
                type: 'success',
                message: ''
              });
              that.searchIfFollow();
              that.loding()
            }
          })
        }).catch(() => {
          that.$message({
            type: 'info',
            message: ''
          });          
        });
      }else{
        this.$axios.get("./addFollow",{
            params:{
              identity1:that.identity1,
              identity2:name,
              username: user,
              password: pass,
            }
          }).then(function(res){
            // console.log(res.data)
            if(res.data == "yes"){
              that.$message({
                type: 'success',
                message: ''
              });
              that.searchIfFollow();
              that.loding()
            }
          })
      }
    },
    searchIfFollow(){   //
      const name = window.sessionStorage.getItem("name");
      const user = window.sessionStorage.getItem("user");
      const pass = window.sessionStorage.getItem("pass");
      var that = this;
      this.$axios.get("./searchIfFollow",{
        params:{
          identity1:that.identity1,
          identity2:name,
          username: user,
          password: pass,
        }
      }).then(function(res){
        // console.log(res.data);
        if(res.data == "yes"){
          that.concernbtnshow = false;
        }else{
          that.concernbtnshow = true;
        }
      })
    }
  }
};
</script>

<style scoped>
.title {
  /**/
  width: 140px;
  display: inline-block;
  position: relative;
  left: 80px;
}
ul {
  list-style: none;
  display: inline-block;
  position: relative;
  top: -20px;
  left: 100px;
}
ul li {
  display: inline-block;
  padding: 0px 20px;
  border-right: 1px solid #c0c4cc;
}

.concernbtn {
  display: inline-block;
  position: relative;
  top:-30px;
  left: 180px;
} /* */

.el-avatar {
  width: 100px;
  height: 100px;
  display: inline-block;
}
</style>